body {
  background-color: whitesmoke;
  margin: 0;
}

.navbar {
  height: 100px;
  background-color: royalblue;
  color: whitesmoke;
  margin-bottom: 15px;
  text-align: center;
  font-size: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.todo-list {
  background-color: white;
  margin: auto;
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #efefef;
  box-shadow: 
    /*The top layer shadow*/
    0 1px 1px rgba(0,0,0,0.15),
    /*The second layer*/
    0 10px 0 -5px #eee,
    /*The second layer shadow*/
    0 10px 1px -4px rgba(0,0,0,0.15),
    /*The third layer shadow*/
    0 20px 0 -10px #eee,
    /*The third layer shadow*/
    0 20px 1px -9px rgba(0,0,0,0.15);
  padding: 30px;
}

.todo-item {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 30px 20px 0;
  width: 70%;
  border-bottom: 1px solid #cecece;
  font-family: Roboto, sans-serif;
  font-weight: 100;
  font-size: 15px;
  color: #333333;
}

input[type=checkbox] {
  margin-right: 10px;
  font-size: 30px;
}

input[type=checkbox]:focus {
  outline: 0;
}

footer {
        position: fixed;
        height: 40px;
        width: 100%;
        background-color: #C1D3D7;
        bottom:0;
    }

    p.copyright {
        position: absolute;
        width: 100%;
        color: black;
        line-height: 15px;
        font-size: 0.9em;
        text-align: center;
    }